
import Chart from "chart.js/auto";
import "chartjs-adapter-moment";

import Modal from "@/components/common/Modal.vue";

import { defineComponent, ref } from "vue";
import moment, { Moment } from "moment";
import DatePicker from "@/components/common/DatePicker.vue";
import { AnimationEvent } from "chart.js/types/animation";
import { ChartConfiguration } from "chart.js";
import { FlexScheduleRequest } from "@/components/FlexSchedule/FlexScheduleInterface";
import Remote from "@/remote/scheduleRequest";
import Dialog from "@/components/dialog";
declare var Affinity: any;

let MyChart = <any>null;

export default defineComponent({
    name: "AnalysisChart",
    components: {
        Modal,
        DatePicker,
    },
    props: {
        modalIndex: {
            type: Number,
        },
    },
    data() {
        return {
            selectedDate: <Moment | null>null,
            dateModal: false,
            dateModalTop: 0,
            dateModalLeft: 0,
            datePickerTargetDay: <Moment | null>moment(),

            chartLabels: <any[]>[],
            chartDatasets: <any[]>[],
            min: 0,
            max: 100,

            canvasHeight: 1,
            // myChart: <any>null,
            myChartHeader: <any>null,
        };
    },

    setup() {
        const myChartRef = ref<InstanceType<typeof HTMLCanvasElement>>();
        const myChartHeaderRef = ref<InstanceType<typeof HTMLCanvasElement>>();

        return { myChartRef, myChartHeaderRef };
    },

    methods: {
        toggleShow() {
            (this.$refs.ModalRef as any).toggleShow();
            // var ctx = (document as any).getElementById('myChart')
        },
        updateDateText(event: any, target: any) {
            this.selectedDate = moment(event.target.value, "DD/MM/YYYY");
            this.dateModal = false;
            // todo: add check for is correct format
        },
        updateDateTime($event: any, d: any) {
            let element = $event.target.getClientRects()[0];
            this.dateModalTop = element.bottom;
            this.dateModalLeft = element.left;

            this.datePickerTargetDay = this.selectedDate;

            this.dateModal = true;
            this.openDatePicker()
                .then((res) => {
                    this.prepareChartData();
                })
                .catch((err) => {
                    console.log("err", err);
                });
        },
        async openDatePicker() {
            let result = (await (this.$refs.DatePickerRef as any).toggleShow(
                this.datePickerTargetDay
            )) as Promise<any>;
            this.selectedDate = result as any;
            return result;
        },

        getData(targetDate: Moment) {
            return this.getDateData(targetDate, targetDate);
        },

        async getDateData(startDate?: Moment, endDate?: Moment): Promise<any> {
            let param = {};
            if (startDate && endDate) {
                param = {
                    employeeNo: Affinity.user.EmployeeNo,
                    startDate: startDate.format("YYYY-MM-DD"),
                    endDate: endDate.format("YYYY-MM-DD"),
                    forManagedEmployees: true,
                };
            }

            return Remote.GetFlexSchedules(param).then((res: any) => {
                return res.FlexSchedules.filter((schedule: any) => {
                    return !!this.$store.state.EmployeeModule.filteredEmployees.find(
                        (employee: any) =>
                            employee.employeeNo === schedule.EmployeeNo
                    );
                });
            });
        },

        staticWorkersPerHour(dataSet: string[][]) {
            let timeDataSet: Moment[][] = dataSet.map((dataSet: string[]) => {
                return [moment(dataSet[0]), moment(dataSet[1])];
            });

            let staticList = Array(24).fill(0);

            for (let timeRange of timeDataSet) {
                let startTime = timeRange[0];
                let endTime = timeRange[1];

                let startWorkingHour = startTime.hour();
                let workingLength = endTime.diff(startTime, "hour");

                for (let i = 0; i < startWorkingHour + workingLength; i++) {
                    if (i >= startWorkingHour) {
                        staticList[i] = staticList[i] + 1;
                    }
                }
            }

            return staticList;
        },

        prepareChartData() {
            let testDate = moment(this.selectedDate);
            Dialog.loading();

            // return;
            this.getData(testDate)
                .then((res) => {
                    Dialog.clear();
                    let schedules = res;

                    let chartData = new Map();

                    for (const schedule of schedules) {
                        if (
                            schedule &&
                            schedule.StartTime &&
                            schedule.EndTime
                        ) {
                            let employeeName = schedule.EmployeeName;
                            let originStartTime = moment(schedule.StartTime);
                            let originEndTimeTime = moment(schedule.EndTime);

                            let cleanStartTime = testDate.clone();
                            cleanStartTime.hour(originStartTime.hour());
                            cleanStartTime.minutes(originStartTime.minutes());

                            let cleanEndTime = testDate.clone();
                            cleanEndTime.hour(originEndTimeTime.hours());
                            cleanEndTime.minutes(originEndTimeTime.minutes());

                            let cleanSche = [
                                cleanStartTime.toISOString(),
                                cleanEndTime.toISOString(),
                            ];

                            chartData.set(employeeName, cleanSche);
                        }
                    }

                    let chartLabels = [...chartData.keys()];
                    let chartDatasets = [...chartData.values()];
                    this.chartDatasets = chartDatasets;

                    let staticList = this.staticWorkersPerHour(chartDatasets);

                    let chartMin = moment(testDate).startOf("day").valueOf();
                    let chartMax = moment(testDate).endOf("day").valueOf();

                    this.canvasHeight = chartLabels.length * 30 + 100;

                    if (MyChart) {
                        if (chartLabels.length <= 0) {
                            let targetCtx =
                                this.myChartHeaderRef!.getContext("2d");
                            targetCtx!.clearRect(
                                0,
                                0,
                                this.myChartHeaderRef!.width,
                                this.myChartHeaderRef!.height
                            );

                            console.info("destroy chart");
                            MyChart.destroy();
                            MyChart = null;
                        } else {
                            console.info("update chart");

                            MyChart.options.scales.x.min = chartMin;
                            MyChart.options.scales.x.max = chartMax;
                            MyChart.options.scales.x1.ticks = {
                                callback: (
                                    value: any,
                                    index: any,
                                    values: any
                                ) => {
                                    return staticList[index];
                                },
                            };
                            MyChart.data.labels = chartLabels;
                            MyChart.data.datasets[0].data = chartDatasets;

                            if (chartLabels.length <= 0) {
                                MyChart.data.labels = [];
                                MyChart.data.datasets = [];
                                MyChart.options.scales.x.min = 0;
                                MyChart.options.scales.x.max = 1;
                            }
                            MyChart.update();
                        }
                    } else if (chartLabels.length <= 0) {
                        // do nothing
                    } else {
                        // create new
                        console.info("create new chart");

                        let ctx = (document as any)
                            .getElementById("myChart")
                            .getContext("2d");
                        let _this = this;
                        let config = {
                            type: "bar",
                            data: {
                                labels: chartLabels,
                                datasets: [
                                    {
                                        label: "Work Hours",
                                        data: chartDatasets,

                                        backgroundColor: [
                                            "rgba(255, 99, 132, 0.2)",
                                            "rgba(54, 162, 235, 0.2)",
                                        ],
                                        borderColor: [
                                            "rgba(255, 99, 132, 1)",
                                            "rgba(54, 162, 235, 1)",
                                        ],
                                        borderWidth: 2,
                                        borderSkipped: false,
                                    },
                                ],
                            },
                            grouped: false,

                            options: {
                                responsive: true,
                                maintainAspectRatio: false,
                                devicePixelRatio: window.devicePixelRatio,
                                indexAxis: "y",
                                scales: {
                                    y: {
                                        stacked: false,
                                    },
                                    x: {
                                        id: "xAxis2",
                                        backgroundColor: "#ffffff",
                                        position: "top",
                                        type: "time",
                                        time: {
                                            unit: "hour",
                                            displayFormats: {
                                                hour: "HH:mm",
                                            },
                                        },
                                        title: {
                                            display: true,
                                            text: "Time",
                                        },
                                        min: chartMin,
                                        max: chartMax,
                                        ticks: {
                                            stepSize: 500000,
                                        },
                                    },
                                    x1: {
                                        id: "xAxis1",
                                        type: "time",
                                        position: "bottom",
                                        gridLines: {
                                            drawOnChartArea: false, // only want the grid lines for one axis to show up
                                        },
                                        time: {
                                            unit: "hour",
                                            displayFormats: {
                                                hour: "HH:mm:ss",
                                            },
                                        },
                                        min: chartMin,
                                        max: chartMax,
                                        title: {
                                            display: true,
                                            text: "Working Employees / Hour",
                                        },
                                        ticks: {
                                            callback: (
                                                value: any,
                                                index: any,
                                                values: any
                                            ) => {
                                                return staticList[index];
                                            },
                                        },
                                    },
                                },
                                plugins: {
                                    title: {
                                        display: false,
                                    },
                                    legend: {
                                        display: false,
                                    },
                                    tooltip: {
                                        usePointStyle: true,
                                        callbacks: {
                                            label: function (tooltipItem: any) {
                                                let timeRange = tooltipItem.raw;
                                                return ` ${moment(
                                                    timeRange[0]
                                                ).format("HH:mm")} - ${moment(
                                                    timeRange[1]
                                                ).format("HH:mm")}`;
                                            },
                                            labelPointStyle: function (
                                                context: any
                                            ) {
                                                return {
                                                    pointStyle: "triangle",
                                                    rotation: 0,
                                                };
                                            },
                                        },
                                    },
                                },
                                animation: {
                                    onComplete: function (
                                        event: AnimationEvent
                                    ) {
                                        let sourceCanvas = (this as any).ctx
                                            .canvas;
                                        let devicePixelRatio =
                                            window.devicePixelRatio;
                                        let copyWidth = (this as any).canvas
                                            .width;
                                        let copyHeight =
                                            (this as any).scales.x.height *
                                            devicePixelRatio;

                                        let targetCtx = (
                                            _this as any
                                        ).myChartHeaderRef!.getContext("2d");

                                        let canvas: any =
                                            document.getElementById(
                                                "myChartHeader"
                                            );

                                        canvas.style.width =
                                            (this as any).canvas.width /
                                                devicePixelRatio +
                                            "px";
                                        canvas.style.height =
                                            copyHeight / devicePixelRatio +
                                            "px";

                                        canvas.width = Math.floor(
                                            (this as any).canvas.width
                                        );
                                        canvas.height = Math.floor(copyHeight);
                                        targetCtx.scale(
                                            devicePixelRatio,
                                            devicePixelRatio
                                        );

                                        targetCtx.drawImage(
                                            sourceCanvas,
                                            0,
                                            0,
                                            copyWidth,
                                            copyHeight,
                                            0,
                                            0,
                                            copyWidth / devicePixelRatio,
                                            copyHeight / devicePixelRatio
                                        );
                                    },
                                },
                            },
                        };
                        MyChart = new Chart(ctx!, config as any) as any;
                    }
                })
                .catch(() => {
                    Dialog.clear();
                    Dialog.alert("Something Wrong. Please try again later");
                });
        },

        cancelModal() {
            if (MyChart) {
                MyChart.destroy();
                MyChart = null;
                this.selectedDate = null;
                this.canvasHeight = 1;
                this.chartDatasets = [];
                // console.log('destroy', MyChart)
            }
        },
    },

    beforeUnmount() {
        if (MyChart) {
            MyChart.destroy();
            MyChart = null;
            this.selectedDate = null;
            this.canvasHeight = 1;
            this.chartDatasets = [];
            // console.log('destroy', MyChart)
        }
    },
});
